.addBusinessWrapper {
  padding: 3%;
}

.addBusinessWrapper h2 {
  text-align: center;
}

.addBusinessForm {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 70px;
  margin: 0 20%;
  box-shadow: 1px 1px 20px rgba(216, 215, 215, 0.74);
  border-radius: 2px;
}

.formContent {
  margin-top: 0.1em;
  margin-bottom: 0.3em;
  font-size: 1.5em;
  min-height: 5rem;
  width: 100%;
}

.addBusinessForm label {
  font-size: 20px;
  font-weight: 600;
}

.addBusinessForm input,
.addBusinessForm textarea {
  border: 1px solid white;
  font-size: 18px;
  font-weight: lighter;
  padding: 5px 0 5px 15px;
  margin: 10px 0 20px;
  border-radius: 1px;
  width: 100%;
}

.formSelect {
  width: 100%;
  background-color: #292a33;
  display: block;
  font-size: 13px;
  outline: none;
  font-weight: 700;
  color: #444;
  line-height: 1.3;
  padding: 0.6em 1.4em 0.5em 0.8em;
  margin: 10px 0 20px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-image: url(data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E),
    linear-gradient(to bottom, #ffffff 0%, #e5e5e5 100%);
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}

.addBusinessForm button {
  background-color: #da5b90;
  color: white;
  font-size: 1.2em;
  font-weight: 700;
  padding: 0.5em 2em;
  text-align: center;
  width: 100%;
  border: none;
  margin-top: 15px;
  border-radius: 2px;
}

.addBusinessForm button:hover {
  background-color: #ee7ead;
  transition: ease-in-out 0.3s;
}
